import React, { FC } from "react";
import { useHistory } from "react-router-dom";
import { Button, Col, Layout, Row, Input, Divider, Image } from "antd";
import {
  UserOutlined,
  LockOutlined,
  EyeInvisibleOutlined,
  EyeTwoTone,
} from "@ant-design/icons";
import "./index.css";
import { Content } from "antd/es/layout/layout";

import AdministrativeKPI from "../../assets/AdministrativeKPI.svg";
import ReviewMeetingScorecards from "../../assets/ReviewMeetingScorecards.svg";
import AcademicKPI from "../../assets/AcademicKPI.svg";
import Login_Img from "../../assets/Login_Img.png";
import Side_Img from "../../assets/image 135.png";

const DetailedDashboard: FC = () => {
  const history = useHistory();

  return (
    <Layout className={"layout-wrapper home-wrapper"}>
      <Content style={{ backgroundColor: "#FFFFFF" }}>
        <Row>
          {/*@ts-ignore*/}
          <Col span={14} style={{ height: "90vh" }}>
            {/* <div style={{ display: "flex", height: "fit-content" }}> */}
            <img
              src={Login_Img}
              height="100%"
              width="100%"
              // style={{ display: "flex" }}
            ></img>
            <img
              src={Side_Img}
              style={{ marginLeft: "-25%" }}
              height="100%"
              width="25%"
            ></img>
            {/* </div> */}
          </Col>
          <Col span={10}>
            <Row
              className={"middle"}
              style={{
                justifyContent: "center",
                height: "100%",
                alignItems: "center",
                flexDirection: "column",
              }}
            >
              <Col>
                <div className="title">
                  <b className="headingb">View Detailed Dashboards</b>
                </div>
                <Button className="DetailsButton">
                  <Row onClick={()=>{history.push('/detailed-academic')}} style={{display:'flex'}}>
                    <Col span={3}><img
                      src={AcademicKPI}
                      alt=""
                    /></Col>
                    <Col offset={4}>Academic KPIs</Col>
                    </Row>
                </Button>
                <Button className="DetailsButton">
                  <Row onClick={()=>{history.push('/detailed-administrative')}} style={{display:'flex'}}>
                    <Col span={3}><img
                      src={AdministrativeKPI}
                      alt=""
                    /></Col>
                    <Col offset={3}> Administrative KPIs</Col>
                    </Row>
                </Button>
                <Button className="DetailsButton">
                  {/*<Row onClick={()=>{history.push('/administrative-kpis/review-meetings')}} style={{display:'flex'}}>*/}
                  <Row onClick={()=>{window.location.href="https://samarthhp-metabase.in/public/dashboard/4ccde962-8c7a-4be6-a5d3-1f5d8ff1b3aa?month=May"}} style={{display:'flex'}}>
                    <Col span={3}><img
                      src={ReviewMeetingScorecards}
                      alt=""
                    /></Col>
                    <Col offset={1}>Review Meeting Scorecards</Col>
                    </Row>
                </Button>
                {/* <div className="form-container">
                <Input
                  className="input"
                  size="large"
                  placeholder="dhe_office"
                  prefix={<UserOutlined className="icon" />}
                />
                <Input.Password
                  className="input"
                  placeholder="password"
                  prefix={<LockOutlined className="icon" />}
                  iconRender={(visible) =>
                    visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
                  }
                />
                <Button className="button" size="large" type="primary" block>
                  Click to Login
                </Button>
                <div style={{ display: "flex" }}>
                  <Button type="link" className="link">
                    Forgot Password
                  </Button>
                  <Button type="link" className="link">
                    <u>Click Here to Reset Password</u>
                  </Button>
                </div>
              </div> */}
              </Col>
            </Row>
          </Col>
        </Row>
      </Content>
      {/* <Row align="middle" justify="space-between" className="footer">
          <Image src={FooterLogo} height={"50px"} />
          <Image src={FooterRightLogo} height={"50px"} />
        </Row> */}
    </Layout>
  );
};

export default DetailedDashboard;
